import React, { useState, useEffect, useCallback } from 'react';
import GIF from 'gif.js';
import UPNG from 'upng-js';
import './app.css';
import { Link } from 'react-router-dom';
import '@uppy/core/dist/style.css';
import '@uppy/dashboard/dist/style.css';
import FrameList from '../frameList/FrameList';
import PenSize from '../tools/penSize/PenSize';
import Tools from '../tools/Tools';
import Preview from '../preview/Preview';
import FPS from '../FPS/FPS';
import Canvas from '../canvas/Canvas';
import Pen from '../tools/Pen';
import PixelPen from '../tools/PixelPen';
import EraseAction from '../tools/EraseAction';
import AllPixelsSameColorAction from '../tools/AllPixelsSameColorAction';
import FillColor from '../tools/FillColor';
import DrawTriangle from '../tools/DrawTriangle';
import DrawCircle from '../tools/DrawCircle';
import DrawRectangle from '../tools/DrawRectangle';
import DrawFillRectangle from '../tools/DrawFillRectangle';
import VerticalMirrorPen from '../tools/VerticalMirrorPen';
import ChooseColor from '../tools/ChooseColor';
import LightenColor from '../tools/LightenColor';
import DarkenColor from '../tools/DarkenColor';
import MoveAction from '../tools/MoveAction';
import DitheringAction from '../tools/DitheringAction';
import CanvasScale from '../canvas/canvasScale/CanvasScale';
import KeyboardShortcuts from '../keyboardShortcuts/KeyboardShortcuts';
import CursorPosition from '../canvas/cursorPosition/CursorPosition';
import {
  getRectCoordsFromEvent,
  getRectSize,
  getCursorPosition,
  download,
  loadImages,
  downloadFile,
} from '../../utils';
import Stroke from '../tools/Stroke';
import usePrev from '../../hooks/usePrev';
import Export from '../export/Export';
import toolNames from '../../toolNames';
import Auth from '../auth/Auth';

const scales = [32, 64, 128];

const APP_VERSION = 1;

const useSetToolAction = () => {
  const [toolAction, setToolAction] = useState(
    {
      isPenSizeAction: false,
      isDrawAction: false,
      isChooseColorAction: false,
      isColorPaletteAction: false,
      isFillColorAction: false,
      isDrawTriangleAction: false,
      isEraseAction: false,
      isPenAction: false,
      isStrokeAction: false,
      isDrawCircleAction: false,
      isDrawRectangleAction: false,
      isDrawFillRectangleAction: false,
      isMirrorPenAction: false,
      isLightenColorAction: false,
      isDarkenColorAction: false,
      isMoveAction: false,
      isDitheringAction: false,
      isAllPixelsSameColorAction: false,
    },
  );

  const toggleToolAction = useCallback((keyName) => {
    const { [keyName]: keyValue, ...newObj } = toolAction;

    Object.keys(newObj).forEach((key) => {
      newObj[key] = false;
    });

    newObj[keyName] = !keyValue;
    setToolAction(newObj);
  }, [toolAction]);

  return { toggleToolAction, toolAction };
};

const useSetPenSizeAction = () => {
  const [penSizeAction, setPenSizeAction] = useState(
    {
      isPenSizeAction1: false,
      isPenSizeAction: false,
    },
  );

  const togglePenSizeAction = (keyName) => {
    const { [keyName]: keyValue, ...newObj } = penSizeAction;

    Object.keys(newObj).forEach((key) => {
      newObj[key] = false;
    });

    newObj[keyName] = !keyValue;
    setPenSizeAction(newObj);
  };

  return { togglePenSizeAction, penSizeAction };
};

const getFromStorage = (key, def) => {
  try {
    return JSON.parse(localStorage.getItem(key)) || def;
  } catch (err) {
    return def;
  }
};

const appendObjectToStorage = (storage, obj) => {
  let state = getFromStorage(storage, {});

  state = {
    ...state,
    ...obj,
  };

  localStorage.setItem(storage, JSON.stringify(state));
};

const defaultKeyboardMap = {
  [toolNames.PEN_TOOL]: 'p',
  [toolNames.ERASER_TOOL]: 'e',
  [toolNames.COLOR_BUCKET_TOOL]: 'b',
  [toolNames.MAGIC_COLOR_BUCKET_TOOL]: 'a',
  [toolNames.COLOR_PICKER_TOOL]: 'o',
  [toolNames.RECTANGLE_TOOL]: 'r',
  [toolNames.CIRCLE_TOOL]: 'c',
  [toolNames.LIGHTEN_TOOL]: 'u',
  [toolNames.DARKEN_TOOL]: 'i',
  [toolNames.DITHERING_TOOL]: 't',
  [toolNames.STROKE_TOOL]: 'l',
  [toolNames.MIRROR_PEN_TOOL]: 'v',
  [toolNames.MOVE_TOOL]: 'm',
  [toolNames.PEN_SIZE_1]: '1',
  [toolNames.PEN_SIZE_2]: '2',
  [toolNames.PEN_SIZE_3]: '3',
  [toolNames.PEN_SIZE_4]: '4',
  [toolNames.FEATHER_PEN_TOOL]: 'd',
  [toolNames.CHANGE_COLORS]: 'x',
  [toolNames.ADD_NEW_FRAME]: 'n',
  [toolNames.FILL_RECTANGLE_TOOL]: 'f',
  [toolNames.DRAW_TRIANGLE_TOOL]: 'h',
};
const initialFrames = [{ num: 0 }];

function App() {
  const [keyboardMap, setKeyboardMap] = useState(defaultKeyboardMap);
  const [framesPerSecond, setFramesPerSecond] = useState(1);
  const [frames, setFrames] = useState(initialFrames);
  const prevFrames = usePrev(frames);
  const [activeFrame, setActiveFrame] = useState(0);
  const prevActiveFrameNum = usePrev(activeFrame);
  const framesNums = frames.map(elem => elem.num);
  const framesNumsObj = {};
  framesNums.forEach((elem) => {
    framesNumsObj[elem] = true;
  });
  const [framesForPreview, setFramesForPreview] = useState(framesNumsObj);
  const { toggleToolAction, toolAction } = useSetToolAction();
  const { togglePenSizeAction, penSizeAction } = useSetPenSizeAction();
  const [penSize, setPenSize] = useState(20);
  const [primaryColor, setPrimaryColor] = useState('#000000');
  const [secondaryColor, setSecondaryColor] = useState('#FFFFFF');
  const [canvasScale, setCanvasScale] = useState(32);
  const [startPos, setStartPos] = useState({ rectXBegin: null, rectYBegin: null });
  const [curPos, setCurPos] = useState({ rectXBegin: 0, rectYBegin: 0 });
  const [rectSize, setRectSize] = useState(getRectSize(640, canvasScale));
  const [isOverCanvas, setIsOverCanvas] = useState(false);
  const [user, setUser] = useState(null);

  const handleLoginSuccess = useCallback((response) => {
    setUser(response.profileObj);
  }, []);

  const handleLogout = useCallback(() => {
    setUser(null);
  }, []);

  const resetState = useCallback(() => {
    setFrames(initialFrames);
    setFramesForPreview({ 0: true });
    setActiveFrame(0);
    setPenSize(20);
    togglePenSizeAction('isPenSizeAction1');
    setCanvasScale(32);
    toggleToolAction('isDrawAction');
    localStorage.removeItem('appstate');
    setKeyboardMap(defaultKeyboardMap);
  }, [togglePenSizeAction, toggleToolAction]);

  const restoreSession = useCallback((st) => {
    if (st.frames) {
      setFrames(st.frames);
    }

    if (st.framesForPreview) {
      setFramesForPreview(st.framesForPreview);
    }

    if (st.activeFrame) {
      setActiveFrame(st.activeFrame);
    }

    if (st.penSize) {
      setPenSize(st.penSize);
      togglePenSizeAction(`isPenSizeAction${st.penSize / rectSize}`);
    } else {
      togglePenSizeAction(`isPenSizeAction${st.penSize / rectSize}`);
    }

    if (st.canvasScale) {
      setCanvasScale(st.canvasScale);
    }

    if (st.activeToolAction) {
      toggleToolAction(st.activeToolAction);
    }

    if (st.keyboardMap) {
      setKeyboardMap(st.keyboardMap);
    }
  }, [rectSize, togglePenSizeAction, toggleToolAction]);

  useEffect(() => {
    const appState = getFromStorage('appstate', {});

    restoreSession(appState);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const editKey = useCallback((keyName) => {
    // eslint-disable-next-line no-alert
    const newKey = window.prompt('Type a new key');
    const values = Object.values(keyboardMap);
    const keyboardMapNext = { ...keyboardMap };

    if (values.indexOf(newKey) !== -1) {
      // eslint-disable-next-line no-alert
      const message = window.confirm('This key has already established. Change it anyway?');
      const oldKey = Object.keys(keyboardMap).find(key => keyboardMap[key] === newKey);

      if (message) {
        keyboardMapNext[oldKey] = '?';
        keyboardMapNext[keyName] = newKey;
      }
    } else {
      keyboardMapNext[keyName] = newKey;
    }

    setKeyboardMap(keyboardMapNext);
  }, [keyboardMap]);

  const handleUnload = useCallback(() => {
    const activeToolAction = Object.keys(toolAction).find(key => toolAction[key]);

    appendObjectToStorage('appstate', {
      frames,
      framesForPreview,
      activeFrame,
      penSize,
      canvasScale,
      activeToolAction,
      keyboardMap,
    });
  }, [activeFrame, canvasScale, frames, framesForPreview, keyboardMap, penSize, toolAction]);

  useEffect(() => {
    window.addEventListener('beforeunload', handleUnload);

    return () => window.removeEventListener('beforeunload', handleUnload);
  }, [handleUnload]);

  const handleFramesChange = useCallback(newFrames => setFrames(newFrames), []);

  const addNewFrame = useCallback(() => {
    const nextFrame = frames.length;
    const newArr = frames.slice();

    newArr.push({ num: nextFrame });
    setFrames(newArr);
    setActiveFrame(nextFrame);

    const copy = Object.assign({}, framesForPreview);
    copy[nextFrame] = true;
    setFramesForPreview(copy);
  }, [frames, framesForPreview]);

  const deleteFrame = useCallback((num) => {
    let newArr = [...frames];

    newArr.splice(num, 1);
    newArr = newArr.map((elem, ind) => ({
      ...elem,
      num: ind,
    }));

    if (newArr.length === 0) {
      newArr.push({ num: 0 });
    }

    if (framesForPreview[num]) {
      const copy = Object.assign({}, framesForPreview);
      delete copy[num];

      Object.keys(copy).forEach((ind) => {
        if (ind > num) {
          copy[ind - 1] = copy[ind];
          delete copy[ind];
        }
      });

      setFrames(newArr);
      setFramesForPreview(copy);
      setActiveFrame(activeFrame - 1);
    }
  }, [activeFrame, frames, framesForPreview]);

  const duplicateFrame = useCallback((num) => {
    let newArr = [...frames];

    newArr.splice(num, 0, newArr[num]);
    newArr = newArr.map((elem, ind) => ({
      ...elem,
      num: ind,
    }));

    const copy = Object.assign({}, framesForPreview);

    Object.keys(copy).forEach((ind) => {
      if (ind > num) {
        copy[+ind + 1] = copy[+ind + 2];
        copy[+ind + 1] = true;
      }
    });
    copy[num + 1] = true;

    setFrames(newArr);
    setActiveFrame(num + 1);
    setFramesForPreview(copy);
  }, [frames, framesForPreview]);

  const addFrameToPreview = useCallback((num) => {
    const copy = Object.assign({}, framesForPreview);

    if (copy[num]) {
      delete copy[num];
    } else {
      copy[num] = true;
    }

    setFramesForPreview(copy);
  }, [framesForPreview]);

  const changeColors = useCallback(() => {
    const currentColor = primaryColor;
    setPrimaryColor(secondaryColor);
    setSecondaryColor(currentColor);
  }, [primaryColor, secondaryColor]);

  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === keyboardMap.PEN_SIZE_1) {
        togglePenSizeAction('isPenSizeAction1');
        setPenSize(rectSize);
      }
      if (e.key === keyboardMap.PEN_SIZE_2) {
        togglePenSizeAction('isPenSizeAction2');
        setPenSize(rectSize * 2);
      }
      if (e.key === keyboardMap.PEN_SIZE_3) {
        togglePenSizeAction('isPenSizeAction3');
        setPenSize(rectSize * 3);
      }
      if (e.key === keyboardMap.PEN_SIZE_4) {
        togglePenSizeAction('isPenSizeAction4');
        setPenSize(rectSize * 4);
      }
      if (e.key === keyboardMap.PEN_TOOL) {
        toggleToolAction('isDrawAction');
      }
      if (e.key === keyboardMap.MIRROR_PEN_TOOL) {
        toggleToolAction('isMirrorPenAction');
      }
      if (e.key === keyboardMap.ERASER_TOOL) {
        toggleToolAction('isEraseAction');
      }
      if (e.key === keyboardMap.COLOR_BUCKET_TOOL) {
        toggleToolAction('isFillColorAction');
      }
      if (e.key === keyboardMap.MAGIC_COLOR_BUCKET_TOOL) {
        toggleToolAction('isAllPixelsSameColorAction');
      }
      if (e.key === keyboardMap.STROKE_TOOL) {
        toggleToolAction('isStrokeAction');
      }
      if (e.key === keyboardMap.RECTANGLE_TOOL) {
        toggleToolAction('isDrawRectangleAction');
      }
      if (e.key === keyboardMap.CIRCLE_TOOL) {
        toggleToolAction('isDrawCircleAction');
      }
      if (e.key === keyboardMap.MOVE_TOOL) {
        toggleToolAction('isMoveAction');
      }
      if (e.key === keyboardMap.LIGHTEN_TOOL) {
        toggleToolAction('isLightenColorAction');
      }
      if (e.key === keyboardMap.DARKEN_TOOL) {
        toggleToolAction('isDarkenColorAction');
      }
      if (e.key === keyboardMap.DITHERING_TOOL) {
        toggleToolAction('isDitheringAction');
      }
      if (e.key === keyboardMap.COLOR_PICKER_TOOL) {
        toggleToolAction('isChooseColorAction');
      }
      if (e.key === keyboardMap.FEATHER_PEN_TOOL) {
        toggleToolAction('isPenAction');
      }
      if (e.key === keyboardMap.ADD_NEW_FRAME) {
        addNewFrame();
      }
      if (e.key === 'q' && e.ctrlKey) {
        deleteFrame(activeFrame);
      }
      if (e.key === 'y' && e.ctrlKey) {
        duplicateFrame(activeFrame);
      }
      if (e.key === 'ArrowUp') {
        if (activeFrame >= 1) {
          setActiveFrame(activeFrame - 1);
        }
      }
      if (e.key === 'ArrowDown') {
        if (activeFrame < frames.length - 1) {
          setActiveFrame(activeFrame + 1);
        }
      }
      if (e.key === keyboardMap.CHANGE_COLORS) {
        changeColors();
      }
      if (e.key === keyboardMap.FILL_RECTANGLE_TOOL) {
        toggleToolAction('isDrawFillRectangleAction');
      }
      if (e.key === keyboardMap.DRAW_TRIANGLE_TOOL) {
        toggleToolAction('isDrawTriangleAction');
      }
    };

    document.addEventListener('keydown', handleKeyDown);

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [
    activeFrame,
    addNewFrame,
    changeColors,
    deleteFrame,
    duplicateFrame,
    frames.length,
    keyboardMap,
    rectSize,
    togglePenSizeAction,
    toggleToolAction,
  ]);

  const mouseUpHandler = (value) => {
    const newArr = frames.slice();
    newArr[activeFrame] = {
      ...newArr[activeFrame],
      image: value,
    };

    setFrames(newArr);
  };

  const onCanvasChange = useCallback((value) => {
    const newArr = frames.slice();
    newArr[activeFrame] = {
      ...newArr[activeFrame],
      image: value,
    };

    setFrames(newArr);
  }, [activeFrame, frames]);

  const handleLoadPiskel = useCallback((e) => {
    const file = e.target.files[0];
    const fileReader = new FileReader();

    fileReader.onload = (fe) => {
      try {
        const { appVersion, piskel } = JSON.parse(decodeURIComponent(fe.target.result));

        if (appVersion !== APP_VERSION) {
          alert('Incompatible files version');

          return;
        }

        restoreSession(piskel);
      } catch (err) {
        alert('Failed to load file');
      }
    };

    fileReader.readAsText(file);
  }, [restoreSession]);

  const handleDownloadPiskelClone = useCallback(() => {
    const data = {
      appVersion: APP_VERSION,
      piskel: {
        activeFrame,
        canvasScale,
        frames,
        framesForPreview,
        keyboardMap,
      },
    };

    downloadFile(JSON.stringify(data), `new_file_${Date.now()}.piskelclone`);
  }, [activeFrame, canvasScale, frames, framesForPreview, keyboardMap]);

  return (
    <div className="wrapper">
      <header>
        <Auth
          clientId="401315303224-6m1l57e8u2i7dj8qhbqqm1jo5psroloe.apps.googleusercontent.com"
          onLoginSuccess={handleLoginSuccess}
          onLoginFail={() => alert('Can\'t login')}
          onLogout={handleLogout}
          user={user}
        />
        <Link className="link-to-landing-page" to="/">
        <span>PISKEL CLONE</span>
        </Link>
      </header>
      <main>
        <aside className="sidebar-left">
          <PenSize
            active={penSizeAction}
            onPenSizeAction={(e) => {
              togglePenSizeAction(`isPenSizeAction${e.target.getAttribute('data-size')}`);
              setPenSize(rectSize * +e.target.getAttribute('data-size'));
            }}
          />
          <Tools
            active={toolAction}
            onDrawAction={() => {
              toggleToolAction('isDrawAction');
            }}
            onFillColorAction={() => {
              toggleToolAction('isFillColorAction');
            }}
            onDrawTriangleAction={() => {
              toggleToolAction('isDrawTriangleAction');
            }}
            onEraseAction={() => {
              toggleToolAction('isEraseAction');
            }}
            onStrokeAction={() => {
              toggleToolAction('isStrokeAction');
            }}
            onChooseColorAction={() => {
              toggleToolAction('isChooseColorAction');
            }}
            onDrawCircleAction={() => {
              toggleToolAction('isDrawCircleAction');
            }}
            onDrawRectangleAction={() => {
              toggleToolAction('isDrawRectangleAction');
            }}
            onDrawFillRectangleAction={() => {
              toggleToolAction('isDrawFillRectangleAction');
            }}
            onMirrorPenAction={() => {
              toggleToolAction('isMirrorPenAction');
            }}
            onLightenColorAction={() => {
              toggleToolAction('isLightenColorAction');
            }}
            onDarkenColorAction={() => {
              toggleToolAction('isDarkenColorAction');
            }}
            onMoveAction={() => {
              toggleToolAction('isMoveAction');
            }}
            onDitheringAction={() => {
              toggleToolAction('isDitheringAction');
            }}
            onPenAction={() => {
              toggleToolAction('isPenAction');
            }}
            onAllPixelsSameColorAction={() => {
              toggleToolAction('isAllPixelsSameColorAction');
            }}
            onPrimaryColorChange={e => setPrimaryColor(e.target.value)}
            onSecondaryColorChange={e => setSecondaryColor(e.target.value)}
            onColorChange={changeColors}
            primaryColor={primaryColor}
            secondaryColor={secondaryColor}
          />
          <FrameList
            activeFrame={activeFrame}
            checkedFrames={framesForPreview}
            frames={frames}
            onAddFrame={addNewFrame}
            onDelete={deleteFrame}
            onDouble={duplicateFrame}
            onChange={handleFramesChange}
            onFrameClick={num => setActiveFrame(num)}
            onNumClick={addFrameToPreview}
          />
          <KeyboardShortcuts
            onChange={editKey}
            keyboardMap={keyboardMap}
          />
        </aside>
        <div className="content">
          <div className="top-canvas">
            {frames[activeFrame] && (
              <Canvas
                clearImage={(prevActiveFrameNum !== activeFrame)
                || (frames[activeFrame].image !== prevFrames[prevActiveFrameNum].image)
                }
                width={640}
                height={640}
                active={toolAction}
                imgSrc={frames[activeFrame].image}
                name="top-canvas"
                onChange={onCanvasChange}
                onMouseDown={(e) => {
                  const pos = getRectCoordsFromEvent(e, canvasScale);

                  setStartPos(pos);
                  setCurPos(pos);
                }}
                onMouseMove={(e) => {
                  setCurPos(getRectCoordsFromEvent(e, canvasScale));
                }}
                onMouseEnter={() => {
                  setIsOverCanvas(true);
                }}
                onMouseLeave={() => {
                  setIsOverCanvas(false);
                }}
                scale={1 / (canvasScale / 32)}
              >
                {toolAction.isDrawAction && (
                  <PixelPen
                    onMouseUp={mouseUpHandler}
                    primaryColor={primaryColor}
                    secondaryColor={secondaryColor}
                    x={curPos.rectXBegin}
                    y={curPos.rectYBegin}
                    width={penSize}
                    height={penSize}
                  />
                )}
                {toolAction.isEraseAction && (
                  <EraseAction
                    onMouseUp={mouseUpHandler}
                    x={curPos.rectXBegin}
                    y={curPos.rectYBegin}
                    width={penSize}
                    height={penSize}
                  />
                )}
                {toolAction.isStrokeAction && (
                  <Stroke
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                    canvasScale={canvasScale}
                    x={startPos.rectXBegin}
                    y={startPos.rectYBegin}
                    rectSize={penSize}
                  />
                )}
                {toolAction.isDrawCircleAction && (
                  <DrawCircle
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                    canvasScale={canvasScale}
                    x={startPos.rectXBegin}
                    y={startPos.rectYBegin}
                    rectSize={penSize}
                  />
                )}
                {toolAction.isDrawRectangleAction && (
                  <DrawRectangle
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                    canvasScale={canvasScale}
                    x={startPos.rectXBegin}
                    y={startPos.rectYBegin}
                    rectSize={penSize}
                  />
                )}
                {toolAction.isDrawFillRectangleAction && (
                  <DrawFillRectangle
                    onMouseUp={mouseUpHandler}
                    primaryColor={primaryColor}
                    secondaryColor={secondaryColor}
                    canvasScale={canvasScale}
                    x={startPos.rectXBegin}
                    y={startPos.rectYBegin}
                    rectSize={penSize}
                  />
                )}
                {toolAction.isMirrorPenAction && (
                  <VerticalMirrorPen
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                    x={curPos.rectXBegin}
                    y={curPos.rectYBegin}
                    width={penSize}
                    height={penSize}
                  />
                )}
                {toolAction.isPenAction && (
                  <Pen
                    onMouseUp={mouseUpHandler}
                    penSize={penSize}
                    currentColor={primaryColor}
                  />
                )}
                {toolAction.isFillColorAction && (
                  <FillColor
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                  />
                )}
                {toolAction.isDrawTriangleAction && (
                  <DrawTriangle
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                    canvasScale={canvasScale}
                    x={startPos.rectXBegin}
                    y={startPos.rectYBegin}
                    rectSize={penSize}
                  />
                )}
                {toolAction.isChooseColorAction && (
                  <ChooseColor
                    onMouseUp={value => setPrimaryColor(value)}
                  />
                )}
                {toolAction.isLightenColorAction && (
                  <LightenColor
                    onMouseUp={mouseUpHandler}
                    canvasScale={canvasScale}
                    penSize={penSize}
                  />
                )}
                {toolAction.isDarkenColorAction && (
                  <DarkenColor
                    onMouseUp={mouseUpHandler}
                    canvasScale={canvasScale}
                    penSize={penSize}
                  />
                )}
                {toolAction.isMoveAction && (
                  <MoveAction
                    onMouseUp={mouseUpHandler}
                    image={frames[activeFrame].image}
                  />
                )}
                {toolAction.isDitheringAction && (
                  <DitheringAction
                    onMouseUp={mouseUpHandler}
                    primaryColor={primaryColor}
                    secondaryColor={secondaryColor}
                    image={frames[activeFrame].image}
                    x={curPos.rectXBegin}
                    y={curPos.rectYBegin}
                    penSize={penSize}
                    column={getCursorPosition(
                      curPos.rectXBegin,
                      curPos.rectYBegin,
                      canvasScale,
                    ).column
                    }
                    row={getCursorPosition(curPos.rectXBegin, curPos.rectYBegin, canvasScale).row}
                  />
                )}
                {toolAction.isAllPixelsSameColorAction && (
                  <AllPixelsSameColorAction
                    onMouseUp={mouseUpHandler}
                    currentColor={primaryColor}
                  />
                )}
              </Canvas>
            )}
          </div>
        </div>
        <aside className="sidebar-right">
          <Preview
            frames={frames.filter(elem => framesForPreview[elem.num])}
            framesPerSecond={framesPerSecond}
            onFullScreenShow={el => el.requestFullscreen()}
          />
          <FPS onChange={value => setFramesPerSecond(value)} />
          <Export
            onPNGExport={() => {
              const link = document.createElement('a');
              link.setAttribute('href', frames[activeFrame].image);
              link.setAttribute('download', 'canvasImage');
              link.click();
            }}
            onPNGSetExport={() => {
              const imageSources = frames.filter(elem => framesForPreview[elem.num]).map(elem => elem.image);
              const link = document.createElement('a');
              imageSources.forEach((imgSrc) => {
                link.setAttribute('href', imgSrc);
                link.setAttribute('download', 'canvasImage');
                link.click();
              });
            }}
            onGIFExport={async () => {
              const imageSources = frames.filter(elem => framesForPreview[elem.num]).map(elem => elem.image);
              const gif = new GIF({
                width: 640,
                height: 640,
              });
              const canvas = document.createElement('canvas');
              canvas.width = 640;
              canvas.height = 640;
              const ctx = canvas.getContext('2d');

              ctx.fillStyle = '#fff';

              const images = await loadImages(imageSources);

              images.forEach((imgEl) => {
                ctx.fillRect(0, 0, 640, 640);
                ctx.drawImage(imgEl, 0, 0);
                gif.addFrame(ctx, { copy: true, delay: 1000 / framesPerSecond });
              });

              gif.on('finished', (blob) => {
                download(URL.createObjectURL(blob), 'New Piskel');
              });

              gif.render();
            }}
            onAPNGExport={async () => {
              const imageSources = frames.filter(elem => framesForPreview[elem.num]).map(elem => elem.image);
              const canvas = document.createElement('canvas');
              canvas.width = 640;
              canvas.height = 640;
              const ctx = canvas.getContext('2d');

              ctx.fillStyle = '#fff';

              const images = await loadImages(imageSources);
              const imagesBuffer = images.map((img) => {
                ctx.fillRect(0, 0, 640, 640);
                ctx.drawImage(img, 0, 0);

                const imageData = ctx.getImageData(0, 0, 640, 640);

                return imageData.data.buffer;
              });

              const pngBuffer = UPNG.encode(imagesBuffer, 640, 640, 0, imagesBuffer.map(() => 1000 / framesPerSecond));
              const blob = new Blob([pngBuffer], { type: 'image/png' });
              const imageUrl = URL.createObjectURL(blob);

              download(imageUrl, 'Piskel.png');
            }}
            onLoadPiskel={handleLoadPiskel}
            onResetClick={resetState}
            onSave={handleDownloadPiskelClone}
          />
          <CanvasScale
            onChange={(e) => {
              const scale = +e.target.value;
              const deltaScale = scale / canvasScale;

              setCanvasScale(scale);
              setRectSize(getRectSize(640, scale));
              setPenSize(penSize / deltaScale);
            }}
            options={scales}
            value={canvasScale}
          />
          <CursorPosition
            isOverCanvas={isOverCanvas}
            x={getCursorPosition(curPos.rectXBegin, curPos.rectYBegin, canvasScale).column + 1}
            y={Math.min(getCursorPosition(curPos.rectXBegin, curPos.rectYBegin, canvasScale).row + 1, canvasScale)}
          />
        </aside>
      </main>
    </div>
  );
}

export default App;
