import {
  useContext,
  useEffect,
  useCallback,
  useRef,
  useState,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import { getRelativeClientX, getRelativeClientY } from '../../utils';

function MoveAction({ onMouseUp, image }) {
  const canvas = useContext(CanvasContext);
  const ctx = canvas.getContext('2d');
  const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  const [startRelativeClientX, setStartRelativeClientX] = useState(0);
  const [startRelativeClientY, setStartRelativeClientY] = useState(0);
  const isMouseDown = useRef(false);
  const img = useRef(null);
  const getImgLink = useCallback(() => {
    isMouseDown.current = false;
    img.current = null;
    ctx.putImageData(imageData, 0, 0);
    onMouseUp(canvas.toDataURL());

    setStartRelativeClientX(0);
    setStartRelativeClientY(0);
  }, [canvas, ctx, imageData, onMouseUp]);
  const handler = useCallback((e) => {
    if (isMouseDown.current) {
      const xRelativeToRect = getRelativeClientX(e);
      const yRelativeToRect = getRelativeClientY(e.target, e.clientY);

      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(img.current, xRelativeToRect - startRelativeClientX, yRelativeToRect - startRelativeClientY);
    }
  }, [canvas, ctx, startRelativeClientX, startRelativeClientY]);

  const toggleMouseDown = useCallback((e) => {
    isMouseDown.current = true;
    img.current = new Image();
    img.current.src = image;

    setStartRelativeClientX(getRelativeClientX(e));
    setStartRelativeClientY(getRelativeClientY(e.target, e.clientY));
  }, [image]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', toggleMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', toggleMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, toggleMouseDown]);

  return null;
}

MoveAction.propTypes = {
  onMouseDown: PropTypes.func,
  image: PropTypes.string,
};

export default MoveAction;
